.container {
  display: flex !important;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  max-width: 500px;
  width: 100%;
  margin: auto;
}
.container > form {
  width: 100%;
  height: 70vh;
  border: 1px solid #dfdfdf;
  padding: 20px;
  border-radius: 5px;
  background: #fff;
  text-align: center;
}

.field > input {
  padding: 10px 70px;
}

.register-form > h1 {
  color: #fff;
}

.register-form > h2 {
  color: #fff;
}

h1 {
  text-align: center;
}

pre {
  width: 70%;
  color: #fff;
  font-size: larger;
}

button {
  padding: 10px 100px;
  color: #fff;
  border-radius: 10px;
  transition-duration: 0.4s;
  background: #0563b4;
}

button:hover {
  background-color: crimson;
  color: white;
}

p {
  color: red;
}
